.about-banner {
    padding: 0 0 5rem 0;
}

.about-banner__title {
    margin-bottom: 2.8rem;
}

.about-banner__title strong {
    color: var(--blue);
    font-weight: 500;
}

.about-banner__inner {
    display: flex;
    align-items: start;
    gap: 4rem;
}

.about-banner__img {
    width: 66.2rem;
    flex-shrink: 0;
    overflow: hidden;
}

.about-banner__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.about-banner__text {
    font-size: 2rem;
    line-height: 1.4;
}

.about-banner__target {
    margin-top: 6rem;
    padding: 3.7rem 6rem 3.7rem 0;
    position: relative;
}

.about-banner__target::after {
    content: '';
    width: 60rem;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--grey);
    z-index: -1;
}

.about-banner__target__title {
    display: flex;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.8rem;
    font-weight: 500;
    margin-bottom: 1.4rem;
}

.about-banner__target__title strong {
    color: var(--blue);
    font-weight: 500;
}

.about-banner__target__title::before {
    content: '';
    width: 3.2rem;
    height: 3.2rem;
    margin-right: 1.5rem;
    background-image: url('../../img/base/check.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    flex-shrink: 0;
}

.about-banner__target__text {
    font-size: 1.6rem;
    line-height: 1.4;
}

@media (max-width: 960px) {
    .about-banner {
        padding: 0 0 4rem 0;
    }

    .about-banner__title {
        margin-bottom: 2rem;
    }


    .about-banner__inner {
        align-items: center;
        flex-direction: column;
        gap: 4rem;
    }

    .about-banner__img {
        width: 100%;
    }


    .about-banner__target {
        margin-top: 2rem;
        max-width: 45rem;
    }

    .about-banner__target::after {
        content: '';
        width: 45rem;
        height: 100%;
        top: 0;
        right: auto;
        left: -1rem;
    }

    .about-banner__target__text {
        font-size: 1.6rem;
        text-wrap: balance;
    }
}

@media (max-width: 500px) {
    .about-banner__text {
        font-size: 1.8rem;
    }
    .about-banner__target {
        padding: 2rem 2rem 2rem 0;
    }
    .about-banner__target__title {
        align-items: start;
    }
    .about-banner__target::after {
        width: 100%;
    }
}

.values__title {
    margin-bottom: 2rem;
}

.values__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
}

.values__list .card {
    padding: 3rem;
}

.values__list .card__icon {
    width: 3.2rem;
    height: 3.2rem;
}

.values__list .card__name {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin-bottom: 1.2rem;
}

@media (max-width: 960px) {

    .values__title {
        margin-bottom: 3rem;
    }

    .values__list {
        grid-template-columns: repeat(1, 1fr);
        gap: 2rem;
    }

}

@media (max-width: 500px) {
    .values__list .card {
        padding: 2rem;
        flex-direction: column;
    }
    .values__list .card__name {
        font-size: 1.4rem;
    }
}

/* Strategy  */
.strategy__inner {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.strategy__icon {
    width: 8rem;
    height: 8rem;
    margin-bottom: 2rem;
    border-radius: 50%;
    border: 1px solid #c5c5c5;
    display: flex;
    align-items: center;
    justify-content: center;
}

.strategy__icon img {
    width: 3.2rem;
    height: 3.2rem;
    object-position: center;
}

.strategy__title {
    margin-bottom: 3rem;
}

.strategy__text {
    font-size: 2rem;
    line-height: 1.4;
    text-align: center;
    margin-bottom: 5rem;
}

.strategy__decor {
    width: 3.2rem;
    height: 8.4rem;
}

.strategy__decor img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}

@media (max-width: 960px) {



    .strategy__title {
        font-size: 2rem !important;
    }

    .strategy__text {
        margin-bottom: 3rem;
    }

}

@media (max-width: 500px) {
    .strategy__title {
        text-align: center;
        margin-bottom: 2rem;
    }
    .strategy__text {
        font-size: 1.6rem;
    }
}

/* Offer  */
.offer__title {
    margin-bottom: 2rem;
}

.offer__list {
    gap: 2rem;
}

.offer__list .card {
    align-items: center;
}

.offer__list .card__name {
    text-wrap: balance;
    font-weight: 500;
    font-family: 'FuturaPT';
}

.main-offer {
    border: 1px solid var(--default);
    padding: 3rem 2.6rem;
    display: flex;
    flex-direction: column;
    gap: 2.7rem;
    border-radius: .8rem;
    grid-area: 2 / 2 /3 /4;
    grid-row: span 2;
    grid-column: span 2;
}

.main-offer__logo {
    width: 10.4rem;
    height: 4rem;
}

.main-offer__logo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}

.main-offer__title {
    font-size: 2.6rem;
    line-height: 1.4;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}

@media (max-width: 960px) {
    .offer__title {
        margin-bottom: 2rem;
    }

    .offer__list {
        gap: 1rem 2rem;
        grid-template-columns: repeat(2, 1fr);
    }


    .main-offer {
        padding: 2.6rem 3rem;
        grid-area: 4 / 1 /5 /2;
        grid-row: span 2;
        grid-column: span 2;
    }

    .main-offer__title {
        font-size: 2.2rem;
    }

    .offer__list .desctop-none {
        display: flex;
    }

}

@media (max-width: 500px) {
    .offer__list {
        grid-template-columns: repeat(1, 1fr);
        gap: 1rem;
    }
    .main-offer {
        padding: 2rem;
        grid-area: auto;
        grid-row: auto;
        grid-column: auto;
        gap: 2rem;
    }
    .main-offer__title  {
        font-size: 1.8rem;
    }
}
